* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.login-form {
  width: 440px;
  height: 280px;
  border: 1px solid #717171;
  flex-direction: row;
  display: flex;
}

.login-form__content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  display: flex;
}

.login-form__content-inputs {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-evenly;
  align-items: center;
  padding: 40px 0;
  display: flex;
}

.login-form__input {
  width: 240px;
}

.login-form__button-reg {
  height: 64px;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  color: #74a1bb;
  background-color: silver;
  border: none;
  font-family: "Exo 2", sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 64px;
  text-decoration: none;
  display: block;
}

.login-form__button-reg:hover {
  color: #4a4a4a;
  background-color: #a0d3f0;
}

.login-form__button-enter {
  width: 110px;
  cursor: pointer;
  color: #717171;
  background-color: silver;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.login-form__button-enter-text {
  text-transform: uppercase;
  writing-mode: vertical-lr;
  text-orientation: upright;
  letter-spacing: -25px;
  margin-top: -25px;
  font-family: "Exo 2", sans-serif;
  font-size: 50px;
  font-weight: 800;
  text-decoration: none;
}

.login-form__button-enter-text:visited {
  color: #717171;
}

.login-form__button-enter:hover, .login-form__button-enter-text:visited:hover {
  color: #9c9c9c;
}

